// src/components/SectionHeader.tsx
'use client';

import { motion } from 'framer-motion';
import React from 'react';

interface SectionHeaderProps {
  title: string;
  subtitle?: string;
  eyebrow?: string;
  className?: string;
  centered?: boolean;
  animated?: boolean;
  gradient?: boolean;
}

const SectionHeader: React.FC<SectionHeaderProps> = ({ 
  title, 
  subtitle, 
  eyebrow,
  className = '',
  centered = true,
  animated = true,
  gradient = true
}) => {
  const headerVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.8,
        ease: "easeOut",
        staggerChildren: 0.2
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.6, ease: "easeOut" }
    }
  };

  const content = (
    <>
      {/* Eyebrow text */}
      {eyebrow && (
        <motion.div
          variants={animated ? itemVariants : {}}
          className={`
            inline-flex items-center gap-2 px-4 py-2 rounded-full mb-4
            bg-white/80 backdrop-blur-md border border-red-200/50 shadow-sm
            ${centered ? 'mx-auto' : ''}
          `}
        >
          <div className="w-2 h-2 bg-gradient-to-r from-red-500 to-green-500 rounded-full animate-pulse" />
          <span className="text-sm font-semibold bg-gradient-to-r from-red-600 to-green-600 bg-clip-text text-transparent uppercase tracking-wide">
            {eyebrow}
          </span>
        </motion.div>
      )}

      {/* Main title */}
      <motion.h2
        variants={animated ? itemVariants : {}}
        className={`
          text-3xl md:text-4xl lg:text-5xl font-extrabold leading-tight mb-4
          ${gradient 
            ? 'bg-gradient-to-r from-gray-900 via-red-600 to-green-600 bg-clip-text text-transparent' 
            : 'text-gray-900'
          }
        `}
      >
        {title}
      </motion.h2>

      {/* Decorative line */}
      <motion.div
        variants={animated ? itemVariants : {}}
        className={`
          w-24 h-1 bg-gradient-to-r from-red-500 to-green-500 rounded-full mb-6
          ${centered ? 'mx-auto' : ''}
        `}
      />

      {/* Subtitle */}
      {subtitle && (
        <motion.p
          variants={animated ? itemVariants : {}}
          className={`
            text-lg md:text-xl text-gray-600 leading-relaxed max-w-3xl
            ${centered ? 'mx-auto' : ''}
          `}
        >
          {subtitle}
        </motion.p>
      )}
    </>
  );

  if (animated) {
    return (
      <motion.div
        variants={headerVariants}
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true, amount: 0.3 }}
        className={`
          ${centered ? 'text-center' : 'text-left'} 
          ${className}
        `}
      >
        {content}
      </motion.div>
    );
  }

  return (
    <div className={`${centered ? 'text-center' : 'text-left'} ${className}`}>
      {content}
    </div>
  );
};

export default SectionHeader;