// src/components/Button.tsx
import Link from 'next/link';
import React from 'react';

type ButtonProps = {
  href?: string;
  variant?: 'primary' | 'secondary' | 'outline' | 'ghost' | 'gradient' | 'glass';
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl';
  children: React.ReactNode;
  className?: string;
  loading?: boolean;
  disabled?: boolean;
  leftIcon?: React.ReactNode;
  rightIcon?: React.ReactNode;
} & React.ButtonHTMLAttributes<HTMLButtonElement>;

const variants = {
  primary: `
    bg-gradient-to-r from-red-600 via-red-500 to-red-600 
    text-white shadow-lg hover:shadow-red-500/25 
    hover:shadow-xl transform hover:-translate-y-1 
    border border-red-500/50 relative overflow-hidden
    before:absolute before:inset-0 before:bg-gradient-to-r 
    before:from-transparent before:via-white/20 before:to-transparent
    before:translate-x-[-100%] hover:before:translate-x-[100%]
    before:transition-transform before:duration-700
  `,
  secondary: `
    bg-gradient-to-r from-green-600 via-green-500 to-green-600 
    text-white shadow-lg hover:shadow-green-500/25 
    hover:shadow-xl transform hover:-translate-y-1 
    border border-green-500/50 relative overflow-hidden
    before:absolute before:inset-0 before:bg-gradient-to-r 
    before:from-transparent before:via-white/20 before:to-transparent
    before:translate-x-[-100%] hover:before:translate-x-[100%]
    before:transition-transform before:duration-700
  `,
  outline: `
    bg-white/80 backdrop-blur-sm text-red-600 border-2 border-red-500/50 
    hover:bg-red-600 hover:text-white hover:border-red-600 
    hover:shadow-lg hover:shadow-red-500/25 transform hover:-translate-y-1
  `,
  ghost: `
    bg-transparent text-gray-700 hover:bg-gray-100 
    hover:text-red-600 border border-transparent 
    hover:border-gray-200
  `,
  gradient: `
    bg-gradient-to-r from-red-600 via-green-500 to-red-600 
    bg-[length:200%_100%] hover:bg-[position:100%_0] 
    text-white shadow-lg hover:shadow-2xl 
    transform hover:-translate-y-1 hover:scale-105
    border border-white/20 relative overflow-hidden
  `,
  glass: `
    bg-white/10 backdrop-blur-md border border-white/20 
    text-white hover:bg-white/20 hover:border-white/30 
    shadow-lg hover:shadow-xl transform hover:-translate-y-1
  `,
};

const sizes = {
  xs: 'px-3 py-1.5 text-xs font-medium',
  sm: 'px-4 py-2 text-sm font-medium',
  md: 'px-6 py-2.5 text-base font-semibold',
  lg: 'px-8 py-3 text-lg font-semibold',
  xl: 'px-10 py-4 text-xl font-bold',
};

export default function Button({
  href,
  variant = 'primary',
  size = 'md',
  children,
  className = '',
  loading = false,
  disabled = false,
  leftIcon,
  rightIcon,
  ...rest
}: ButtonProps) {
  const baseClasses = `
    inline-flex items-center justify-center gap-2 
    rounded-full font-semibold transition-all duration-300 
    focus:outline-none focus:ring-4 focus:ring-red-500/20 
    disabled:opacity-50 disabled:cursor-not-allowed
    disabled:transform-none disabled:shadow-none
    relative z-10
  `;

  const classes = `${baseClasses} ${variants[variant]} ${sizes[size]} ${className}`;

  const content = (
    <>
      {loading && (
        <svg 
          className="animate-spin -ml-1 mr-2 h-4 w-4" 
          xmlns="http://www.w3.org/2000/svg" 
          fill="none" 
          viewBox="0 0 24 24"
        >
          <circle 
            className="opacity-25" 
            cx="12" 
            cy="12" 
            r="10" 
            stroke="currentColor" 
            strokeWidth="4"
          />
          <path 
            className="opacity-75" 
            fill="currentColor" 
            d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
          />
        </svg>
      )}
      {!loading && leftIcon && <span className="flex-shrink-0">{leftIcon}</span>}
      <span className="relative z-10">{children}</span>
      {!loading && rightIcon && <span className="flex-shrink-0">{rightIcon}</span>}
    </>
  );

  if (href) {
    return (
      <Link href={href} className={classes}>
        {content}
      </Link>
    );
  }

  return (
    <button 
      className={classes} 
      disabled={disabled || loading}
      {...rest}
    >
      {content}
    </button>
  );
}